<template>
    <div id="invoice" class="ma-4">
        <v-container class="container" v-if="invoiceLoaded">
            <!-- Actions  -->
            <ContentHeader :name="name" :totalAmount="invoiceData.length" :mountedAct="handleClick"/>

            <!-- List Invoices  -->
            <div>
                <Invoice
                    v-for="(invoice,index) in invoiceData"
                    :key="index"
                    :data="invoice"
                />
            </div>
        </v-container>
        <!-- <ContentHeader :name="skeletonObj.name" :totalAmount="skeletonObj.total" /> -->
        <Skeleton :name="name" v-else/>
        <Dialog />
    </div>
</template>

<script>
import { mapState, mapActions, mapMutations } from 'vuex';
import ContentHeader    from '@/components/ContentHeader';
import Invoice          from '@/components/Elements/invoice_e';
import Skeleton         from '@/components/Skeleton/invoice_skeleton';
import Dialog           from '@/components/Dialogs/invoice_dialog';

export default {
    components: { ContentHeader, Invoice, Skeleton, Dialog },
    data() {
        return {
            name: 'invoices',
        }
    },
    created(){
        this.GET_INVOICES();
    },
    computed: {
        ...mapState({
            invoiceLoaded:  state => state.invoices.invoiceLoaded, //https://stackoverflow.com/questions/54791157/i-cant-any-get-data-using-mapstate-in-vue-using-vuex
            invoiceData:    state => state.invoices.invoiceData,
            dialog:         state => state.invoices.dialog
        })
    },
    methods: {
        ...mapMutations(['UPDATE_DIALOG']),
        ...mapActions(['GET_INVOICES']),

        handleClick(){
            this.UPDATE_DIALOG();
        }
    }
}
</script>

