<template>
  <router-link
    :to="{name: 'InvoiceView', params: {invoiceId: invoice.invoiceId}}" class="flex">
        <v-card
            class="mb-3 invoice"
            :class="{ paid: invoice.invoicePaid, draft: invoice.invoiceDraft, pending: invoice.invoicePending }"
        >
            <v-card-text>
                <v-layout row wrap class="pa-3">
                    <v-flex xs12 md3>
                        <p class="headline mt-3">#{{ invoice.invoiceId }}</p>
                    </v-flex>
                    <v-flex xs4 md3>
                        <span class="caption grey--text">Due Date:</span>
                        <p class="subheader">{{ outputDueDate }}</p>
                    </v-flex>
                    <v-flex xs4 md3>
                        <span class="caption grey--text">Client:</span>
                        <p class="subheader">{{ invoice.clientName }}</p>
                    </v-flex>
                    <v-flex xs4 md1>
                        <span class="caption grey--text">Total:</span>
                        <p class="subheader">${{ invoice.invoiceTotal }}</p>
                    </v-flex>
                    <v-flex xs12 md2>
                        <div class="invoice-chip">
                            <v-chip
                                label
                                class="mt-3"
                                :class="{ paid: invoice.invoicePaid, draft: invoice.invoiceDraft, pending: invoice.invoicePending }"
                            >
                                <v-icon left>mdi-circle-medium</v-icon>
                                <span v-if="invoice.invoicePaid">Paid</span>
                                <span v-if="invoice.invoiceDraft">Draft</span>
                                <span v-if="invoice.invoicePending">Pending</span>
                            </v-chip>
                            <v-spacer></v-spacer>
                            <v-icon
                                right
                                class="ml-0"
                            >mdi-chevron-right</v-icon>
                        </div>
                    </v-flex>
                </v-layout>
            </v-card-text>
        </v-card>
      <!-- icon  -->
  </router-link>
</template>

<script>
export default {
  name: "invoice",
  props: ["data"],
  data() {
      return {
          invoice:          this.data,
          outputDueDate:    null
      }
  },
  created(){
      if(this.invoice){
          let result = this.invoice.paymentDueDate;
          result = result.toDate();
          result = result.toLocaleDateString("en-us", { year: "numeric", month: "short", day: "numeric"})
          this.outputDueDate = result;
      }
  }
}
</script>

